import Link from 'next/link'

const linkStyle = {
  marginRight: 15
}

const contentM = {
  float: 'left',
  width: '260px',
  backgroundColor: 'red'
}

const Sidebar = () => (
    <div style={contentM}>
        <Link href="/">
          <a style={linkStyle}>Truc</a>
        </Link>
    </div>
)

export default Sidebar



